import React, { useContext } from 'react';
import { Link } from 'gatsby';
import { Grid, makeStyles, Theme, createStyles } from '@material-ui/core';
import { GlobalMenus } from 'src/constants';
import Logo from 'components/Utils/LubyconLogo';
import { useAuth } from 'hooks/auth/useAuth';
import RouteLink from 'components/Utils/RouteLink';
import UserMenu from 'components/Header/UserMenu';
import red from '@material-ui/core/colors/red';
import { GlobalContext } from 'src/stores';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      padding: '1rem',
    },
    menus: {
      display: 'flex',
      padding: 0,
      margin: 0,
      alignItems: 'center',
      height: '100%',
    },
    menuItems: {
      fontSize: '1rem',
      listStyle: 'none',
      marginRight: theme.spacing(2),
      [theme.breakpoints.down('md')]: {
        fontSize: '0.8rem',
        marginRight: theme.spacing(1),
      },
    },
    headerLink: {
      display: 'flex',
      alignItems: 'center',
      textDecoration: 'none',
    },
    link: {
      color: theme.palette.text.secondary,
      textDecoration: 'none',
      transition: 'color 0.2s ease-in',
      '&:hover': {
        color: red['400'],
      },
      [theme.breakpoints.down('md')]: {
        fontSize: '0.7rem',
      },
    },
  })
);

const Header = () => {
  const { isAuthorized } = useAuth();
  const { themeType } = useContext(GlobalContext);
  const styles = useStyles();
  return (
    <header className={styles.root}>
      <Grid justify="space-between" alignItems="center" container>
        <Grid xs="auto" item>
          <Link className={styles.headerLink} to="/">
            <Logo color={themeType} />
          </Link>
        </Grid>
        <Grid xs="auto" item>
          <ul className={styles.menus}>
            {GlobalMenus.map((menu, index) => (
              <li key={index} className={styles.menuItems}>
                <RouteLink to={menu.link} className={styles.link}>
                  {menu.name}
                </RouteLink>
              </li>
            ))}
            <UserMenu />
            <li className={styles.menuItems}>
              {!isAuthorized && (
                <RouteLink to="/app/login" className={styles.link}>
                  LOGIN
                </RouteLink>
              )}
            </li>
          </ul>
        </Grid>
      </Grid>
    </header>
  );
};

export default Header;
