import React, { useMemo } from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import Image from 'gatsby-image';
import { ImageSharp } from 'src/models';
import { PaletteType, makeStyles, Theme, createStyles } from '@material-ui/core';

const useStyles = makeStyles<Theme, { size: number }>(theme =>
  createStyles({
    root: {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
    },
    image: {
      marginRight: '0.5rem',
      width: props => `${props.size}px`,
    },
    text: {
      color: theme.palette.text.secondary,
      fontSize: props => `${props.size / 23}rem`,
      [theme.breakpoints.down('md')]: {
        display: 'none',
      },
    },
  })
);

interface QueryResult {
  logoImage: ImageSharp;
  logoWhiteImage: ImageSharp;
}

interface Props {
  size?: number;
  color?: PaletteType;
}
const LubyconLogo = ({ size = 30, color = 'light', ...rest }: Props) => {
  const styles = useStyles({ size });
  const { logoImage, logoWhiteImage }: QueryResult = useStaticQuery(graphql`
    query LogoQuery {
      logoImage: file(absolutePath: { regex: "/logo.png/" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
      logoWhiteImage: file(absolutePath: { regex: "/logo_white.png/" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  const logo = useMemo(() => {
    if (color === 'light') {
      return logoImage;
    } else {
      return logoWhiteImage;
    }
  }, [color, logoImage, logoWhiteImage]);

  return (
    <div className={styles.root} {...rest}>
      <Image fluid={logo.childImageSharp.fluid} className={styles.image} alt="Lubycon 로고" />
      <span className={styles.text}>LUBYCON</span>
    </div>
  );
};

export default LubyconLogo;
